
.bread{
    height: 45px;
    line-height: 45px;
    width: 996px;
    margin: 0 auto;
}
.bread-wrap{
    padding: 0 10px;
    font-size: 14px;
    color: #a1a1a1;
}
.bread-wrap a{
  position: relative;
    margin-right: 20px;
}
.bread-wrap a:after{
    position: absolute;
    top: 0px;
    content:'/';
    height:20px;
    line-height: 20px;
}
.bread-wrap span{
    color:#d1434a;
}



