
.lunbo[data-v-086bd3a2]{
	width: 100%;
    height: 400px;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}
.lunbo .slide[data-v-086bd3a2]{
	width: 100%;
    height: 400px;
}
.lunbo .slide img[data-v-086bd3a2]{
	width: 100%;
	height: 400px;
	min-width: 1200px;
}
.lunbo li[data-v-086bd3a2]{
	position: absolute;
}
.bullet[data-v-086bd3a2]{
	width: 100%;
    position: absolute;
    /*bottom: 10px;*/
    margin: 0 auto;
    text-align: center;
   	z-index: 10;
   	top: 350px;
}
span[data-v-086bd3a2]{
    width: 20px;
    height: 5px;
    border: 1px solid;
    background: grey;
    display: inline-block;
    margin-right: 10px;
}
.active[data-v-086bd3a2] {
    background: red;
}
.image-enter-active[data-v-086bd3a2]{
	-webkit-transform: translateX(0);
	        transform: translateX(0);
	-webkit-transition: all 1s ease;
	transition: all 1s ease;
}
.image-leave-active[data-v-086bd3a2]{
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
	-webkit-transition: all 1s ease;
	transition: all 1s ease;
}
.image-enter[data-v-086bd3a2]{
	-webkit-transform: translateX(100%);
	        transform: translateX(100%);
}
.image-leave[data-v-086bd3a2]{
	-webkit-transform: translateX(0);
	        transform: translateX(0);
}



