
ul li[data-v-61c22137]{
	list-style: none;
}
.navigation_panel_wrapper[data-v-61c22137]{
	text-align: justify;
    margin: 10px 0;
}
.refinement_header[data-v-61c22137]{
	display: inline-block;
    font-size: 28px;
    font-family: SimHei;
    text-transform: uppercase;
    line-height: 90%;
}
.plp_navigation_panel[data-v-61c22137]{
	background: transparent;
    border: 0;
    display: inline-block;
    float: none;
    text-align: left;
    width: auto;
}
.plp_navigation_panel a[data-v-61c22137]{
	font-size: 14px;
    letter-spacing: .25px;
    text-transform: uppercase;
}
.plp_navigation_panel li[data-v-61c22137]{
	margin: 0 14px;
	padding: 0;
	position: relative;
	float: left;
}
.plp_navigation_panel li a[data-v-61c22137]:after{
	content: "";
    position: absolute;
    left: -17px;
    top: 2px;
    width: 1px;
    height: 11px;
    background: #666;
}
.plp_navigation_panel li:first-child a[data-v-61c22137]:after{
	display: none;
}
a[data-v-61c22137]{
	text-decoration: none;
	color: #000;
}
.refinement_filter_by_box[data-v-61c22137]{
	overflow: hidden;
	border-top: 1px solid #000;
    border-bottom: 1px solid #000;
}
.refinement_filter_by[data-v-61c22137]{
	display: block;
    background: 0;
    font-size: 24px;
    font-family: SimHei;
    text-transform: uppercase;
    text-align: center;
    padding: 12px 0;
    margin: 0 -30px;
    letter-spacing: 1px;
}
.refinement_filter_by span[data-v-61c22137]{
	cursor: pointer;
    display: inline-block;
}
.refinement_filter_by span[data-v-61c22137]:after{
	content: "";
    display: inline-block;
    margin-left: 17px;
    width: 21px;
    height: 21px;
    vertical-align: top;
    background: url(/static/images/icons-sprite-25x25.png) no-repeat -141px -134px transparent;
}
.filter_section_wrap[data-v-61c22137]{
	float: left;
    clear: both;
    width: 100%;
    border-bottom: 1px solid #000;
    border: 1px solid transparent;
}
.filter_section_wrap .container[data-v-61c22137]{
	width: auto;
	margin: 0 auto;
	padding: 0;
}
.filter_section_wrap .container .toggle[data-v-61c22137]{
	cursor: pointer;
}
.refinement[data-v-61c22137]{
	display: inline-block;
    vertical-align: top;
    margin-bottom: 8px;
}
.refinement_headline[data-v-61c22137]{
	display: block;
    min-width: 111px;
    max-width: 200px;
    padding: 0 10px;
    margin: 0 0 26px 0;
    font-size: 14px;
    font-weight: normal;
    font-family: SimHei;
    text-transform: uppercase;
    background: 0;
    margin-bottom: 12px;
}
.refinement_color[data-v-61c22137]{
	width: 170px;
}
.hidden[data-v-61c22137]{
	display: none;
}
.refinement_list_link[data-v-61c22137]{
	display: block;
    font-size: 12px;
    text-transform: uppercase;
    font-family: SimHei;
    padding: 2px 25px 3px 10px;
    line-height: 22px;
}
.refinement_sort_by[data-v-61c22137]{
	height: 36px;
    display: block;
    margin: 12px -30px 17px;
    padding: 10px 30px 12px;
    background: rgba(0,0,0,0.06);
    line-height: 36px;
}
.sortby_pc[data-v-61c22137],.sort_by_list_item[data-v-61c22137]{
	float: left;
	font-weight: bold;
    letter-spacing: .25px;
    text-transform: uppercase;
    font-family: SimHei;
}
.refinement_sort_by .refinement_list_item[data-v-61c22137] {
	margin-left: 15px;
	font-size: 14px;
}
/*筛选*/
.swatch_beiges .swatchBgColor[data-v-61c22137]{
	display: block;
	width: 28px;
	height: 28px;
	float: left;
	border-radius: 50%;
	margin: 5px;
}
