
.fl{
	clear:both;
}
.register{
	margin-top: 20px;
	font-size: 14px;
}
.center{
	width: 996px;
	margin: 0 auto;
}
.center .center-left{
	width: 350px;
	height: 700px;
	float: left;
}
.center .center-left .gender{
	font-size: 11px;
}
.center .center-left .gender input{
	vertical-align: text-bottom;
	font-size: 10px;
}
.center .center-left p{
	margin-top: 20px;
	line-height: 20px;
}
.center .center-left .mandatory input{
	width: 300px;
	height: 35px;
}
.center .center-left button{
	width: 200px;
	height: 30px;
	background-color:#000 ;
	margin-top: 30px;
	color:white;
}
.center .center-center{
	width: 350px;
	height: 700px;
	border-right:1px solid #ccc;
	float: left;
}
.center .center-center p{
	line-height: 100px;
	text-align: center;
	padding-top: 50px;
}
.center .center-center span{
	display: block;
	text-align: center;
}
.center .center-center .login{
	margin-top: 30px;
	margin-left: 30px;
}
.center .center-center .login a>span{
	display: inline-block;
	width: 20px;
	height: 20px;
	vertical-align: middle;
}
.center .center-right{
	width: 200px;
	height: 700px;
	float: right;
}
.center .center-right>p{
	font-size: 13px;
	line-height: 40px;
}
.center .center-right span{
	font-size: 13px;
	line-height: 40px;
	font-size: 17px;
}
.center .center-right .information{
	margin-top: 20px;
}
.center .center-right .information>p{
	font-size: 10px;
	line-height: 20px;
	color:#999999;
}
.center .center-right .fenge{
	width: 250px;
	height: 10px;
	margin: -6px auto;
	background: url('../../static/image/a7.png') no-repeat;
	background-position: -327px -51px;
}
.center .center-right hr{
	margin-top: 10px;
	border:1px solid #000;
}
.center .service strong{
	display: inline-block;
	padding-top: 30px;
}
.center .service>div{
	margin-top: 10px;
}
.center .service>div span{
	display: inline-block;
	width: 20px;
	height: 20px;
	vertical-align: middle;
	background: url('../../static/image/a7.png') no-repeat;
	background-position: -476px 2px;
}
.center .service p{
	font-size: 10px;
	padding-top: 10px;
	color:#999999;
}



