
body{
	font-family:"SimHei,sans-serif";
}
.header-footer{
	background-color: #fff;
    width: 100%;
    height: 53px;
}
.header-footer .container{
	width: 100%;
  	margin:0 auto;
  	height: 53px;
}
.tab{
	width: 996px;
	margin: 0 auto;
	padding: 0 30px;
	height: 53px;
}
.tab>li{
	display: inline-block;
	list-style: none;
	float: left;
}
.tab>li a{
	cursor: pointer;
	position: relative;
    display: block;
    padding: 0 33px 4px 33px !important;
    font-size: 18px;
    line-height: 49px;
    color: #070707;
    text-transform: uppercase;
    letter-spacing: .5px;
}
.tab>li a:before{
	content: "";
    display: block;
    width: 5px;
    height: 5px;
    background: url(/static/images/icons-sprite-25x25.png) no-repeat -75px -75px transparent;
    position: absolute;
    top: 50%;
    left: -7px;
    margin-top: -5px;
    font-weight: bold;
}
.tab>li:first-child a:before{
	display: none;
}
.tab li a:hover {
	color: orange;
}
.xiangqing{
	width: 100%;
	height: 400px;
	background-color: white;
	position: absolute;
	z-index: 20;
	left: 0;
	display: none;
	border-top:2px solid #000;
}
.tab li:hover .xiangqing{
	display: block;
}
.tab li .xiangqing>h2{
	margin-left: 100px;
	font-size: 15px;
	line-height: 30px;
}
.tab li .xiangqing ul li{
	float: left;
	width: 150px;
	height: 300px;
	/*border:1px solid red;*/
	margin-left: 100px;
	list-style: none;
	font-size: 13px;
	line-height: 30px;
}
.tab li .xiangqing ul li p{
	font-size: 13px;
}
.tab li .xiangqing ul div{
	width: 300px;
	height: 300px;
	margin-left: 200px;
}
.tab li .xiangqing ul div img{
	position: absolute;
	left: 200px;
}



